import { Component } from "react";
import { Button, Tab, Tabs, Card, Nav, OverlayTrigger, Tooltip } from "react-bootstrap";
import Grid_2x2_Code from "./Components/GridCode/Grid_2x2_Code";
import Grid_2x3_Code from "./Components/GridCode/Grid_2x3_Code";
import Grid_3x3_Code from "./Components/GridCode/Grid_3x3_Code";
import Grid_4x1_Code from "./Components/GridCode/Grid_4x1_Code";
import EmailPlatformInstructions from './Components/EmailPlatformInstructions';
import { COPY_TO_CLIPBOARD, OPEN_INTERCOM } from "../../../../utils";
import { FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import ShareCodeModal from "../CommonComponents/ShareCodeModal";
import Grid from "./Components/Grid/Grid";


class CodePreview extends Component {

    state = { copied: false, openPopup: false, previewKey: 'script', }

    copyText = (id) => {
        COPY_TO_CLIPBOARD(id)
        this.setState({ copied: true })
        setTimeout(() => { this.setState({ copied: false }) }, 1000)
    }

    emailViewCode = (gridType) => {
        const { campaignLayout, spaceVal } = this.props
        switch (gridType) {
            case 1:
                return <Grid_2x2_Code data={campaignLayout} spaceVal={spaceVal} />
            case 2:
                return <Grid_2x3_Code data={campaignLayout} spaceVal={spaceVal} />;
            case 3:
                return <Grid_3x3_Code data={campaignLayout} spaceVal={spaceVal} />;
            case 4:
                return <Grid_4x1_Code data={campaignLayout} spaceVal={spaceVal} />;
            default:
                return <Grid_4x1_Code data={campaignLayout} spaceVal={spaceVal} />;
        }
    }

    render() {
        const { selectedPlatform, layout, campaignLayout, spaceVal } = this.props;
        const { copied, openPopup, previewKey } = this.state
        const emailContent = window?.APP_CONTENT?.emailCampaign ?? {}

        return <Card className="preview_ p_header_">
            <Tab.Container id="left-tabs-example" defaultActiveKey="code" onSelect={event => this.setState({ previewKey: event })}>
                <Card.Header className="py-0 align-items-center justify-content-center w-100">
                    {/* <Nav variant="underline" className="align-items-end">
                        <Nav.Item className="me-3">
                            <Nav.Link eventKey="code">Genrated Code</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="preview">Preview</Nav.Link>
                        </Nav.Item>
                    </Nav> */}
                    <Nav className='gap-0'>
                        <Nav.Item className=''>
                            <Nav.Link eventKey="code" className={`${previewKey === 'script' ? 'btn-primary' : 'btn-outline-primary'} btn rounded-0 rounded-start`}>
                                <FontAwesomeIcon icon="code" className="me-2" />
                                {emailContent.codeTabLabel ?? ""}
                            </Nav.Link>
                        </Nav.Item>
                        <Nav.Item className=''>
                            <Nav.Link eventKey="preview" className={`${previewKey === 'preview' ? 'btn-outline-primary' : 'btn-primary'} btn border-start-0 rounded-0 rounded-end`}>
                                <FontAwesomeIcon icon="eye" className="me-2" />
                                {emailContent.previewTabLabel ?? ""}
                            </Nav.Link>
                        </Nav.Item>
                    </Nav>
                </Card.Header>
                <Tab.Content>
                    <Tab.Pane eventKey="code">
                        <Card.Body className="pb-10">
                            <div className="mb-10">
                                <h5 className="mb-6">{emailContent.publishHtmlTitle ?? ""}</h5>
                                <div className="d-flex align-items_center mb-5">
                                    <span className="h-22px w-22px bg-success text-white rounded-circle text-center me-2 fw-bold f-center" style={{ marginTop: -2 }}>1</span>
                                    <h6 className="mb-0">{emailContent.copyCodeInstruction ?? ""}</h6>
                                </div>
                                <div className="position-relative mb-7 embed_code embed_code_box iframe_embcode_box rounded-3">
                                    <div className='embed-border p-2'>
                                        <code className="form-control textarea-min-row-4 p-lg-4 fs-9 embed-border" id="emailEmbedCode_" onClick={() => { this.copyText('emailEmbedCode_') }}>
                                        {this.emailViewCode(layout)}
                                        </code>
                                    </div>
                                    <div className="position-absolute end-0 bottom-0 pe-5 pb-5">
                                        <OverlayTrigger placement="top" overlay={<Tooltip>{!copied ? (emailContent.copyCodeTooltip ?? "") : (emailContent.codeCopiedTooltip ?? "")}</Tooltip>}>
                                            <Button variant="link" className="fs-6 me-4 text-gray-700" onClick={() => this.copyText('emailEmbedCode_')}>
                                                <FontAwesomeIcon type={'solid'} icon={!copied ? 'copy' : 'check'} iconClass={copied ? 'text-success' : ''} />
                                            </Button>
                                        </OverlayTrigger>
                                        <OverlayTrigger placement="top" overlay={<Tooltip>{emailContent.shareCodeTooltip ?? ""}</Tooltip>}>
                                            <Button variant="link" className="text-gray-700 fs-6 GetShareCode" onClick={(e) => this.setState({ openPopup: true })}>
                                                <FontAwesomeIcon type={'solid'} icon={'envelope'} iconClass="me-0" />
                                            </Button>
                                        </OverlayTrigger>
                                    </div>
                                </div>
                                <div className="d-flex align-items_center mb-5">
                                    <span className="h-22px w-22px bg-success text-white rounded-circle text-center me-2 fw-bold f-center" style={{ marginTop: -2 }}>2</span>
                                    <h6 className="mb-0">{emailContent.pasteCodeInstruction ?? ""}</h6>
                                </div>
                                <EmailPlatformInstructions platform={selectedPlatform} instructionsData={emailContent} />
                            </div>
                            <Card.Footer className="f-center rounded-0 justify-content-start position-absolute bottom-0 start-0 w-100 bg-body" style={{ height: 65 }}>
                                <div className="py-1 px-3 rounded-pill badge-light-primary text-nowrap d-flex flex-wrap align-items-center">
                                    <p className="my-1 text-muted d-md-inline d-none">{emailContent.supportHelpText ?? ""}</p>
                                    <p className="my-1 text-muted d-md-none d-inline">{emailContent.helpText ?? ""}</p>
                                    <FontAwesomeIcon icon={'angle-right'} iconClass="mx-2" />
                                    <Button variant='link' className='f-center d-flex' onClick={OPEN_INTERCOM}>
                                        <FontAwesomeIcon type={'solid'} icon={'comments'} iconClass='me-1' />
                                        {emailContent.connectSupportButton ?? ""}
                                    </Button>
                                </div>
                            </Card.Footer>
                        </Card.Body>
                    </Tab.Pane>
                    <Tab.Pane eventKey="preview">
                        <Card.Body className="f-center flex-wrap">
                            <div className="mx-auto w-100 p-3 border rounded-1" style={{ maxWidth: 500 }}>
                                <Grid size={layout} data={campaignLayout} spaceVal={spaceVal} />
                            </div>
                        </Card.Body>
                    </Tab.Pane>
                </Tab.Content>
            </Tab.Container>
            {openPopup ? <ShareCodeModal type={1} text={emailContent.shareCodeModalText ?? ""} heading={emailContent.shareCodeModalHeading ?? ""} onClose={(e) => this.setState({ openPopup: false })} codeId={'#emailEmbedCode_'} /> : null}
        </Card>
    }
}

export default CodePreview