import { Component } from "react";
import { Button, Form, Row, Col, Image } from "react-bootstrap";
import { baseUrl, FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { connect } from "react-redux";
import RangeInput from "../../CommonComponents/RangeInput";
import { checkPermission } from "../../../../../actions/collaboratorActions";
import RequestButtonHOC from "../../CommonComponents/RequestButtonHOC";

class AsideContent extends Component{
    render(){
        const {spaceVal, layout, changeSpace, selectLayout, postError} = this.props
        const GridSize = ["2X2", "2X3", "3X3", "1X4"];
        const emailContent = window?.APP_CONTENT?.emailCampaign ?? {}

        return <>
                <div className='border-bottom px-4 py-4 mb-4'>
                    <div className="mb-3">
                        <Form.Label htmlFor="mail_range">{emailContent.spacingLabel ?? ""}</Form.Label>
                        <RangeInput id="mail_range" min={0} max={20} step={1} value={spaceVal} onChange={(e)=>changeSpace(e)} inheritStyle={!checkPermission('publish', 'email_camp',true,true)}/>
                    </div>
                </div>
                <div className='px-4 pb-3 mb-4'>
                    <Form.Label>{emailContent.chooseLayoutLabel ?? ""}</Form.Label>
                    <Row className="select-group flex-wrap row gx-8 border-top px-4">
                        {GridSize.map((gird, index) => (
                            <Col className="col-4 px-1 select-grid mt-2">
                                <Button key={index} variant="grid" data-grid={gird} className={`border mail_layout rounded d-flex align-items-center justify-content-center flex-column p-1 w-100 ${layout == index+1 ? 'active' : ''}`} onClick={() => selectLayout(index+1)} disabled={!checkPermission('publish', 'email_camp',true,true)}>
                                    <div className="w-100 h-50px mb-2 p-2 f-center">
                                        <Image className="" src={baseUrl(`media/icons/email-layout/${index+1}.svg`)} alt={gird} fluid/>
                                    </div>
                                    <p className="mb-0 text-center fs-10">{gird} Grid</p>
                                </Button>
                            </Col>
                        ))}
                        <Col className="col-4 px-1 select-grid mt-2">
                            <RequestButtonHOC title={emailContent.requestNewGridTitle ?? ""}>
                                <Button variant="grid" className={`border mail_layout rounded d-flex align-items-center justify-content-center flex-column p-1 w-100`}>
                                    <div className="w-100 h-50px mb-2 p-2 f-center">
                                        <FontAwesomeIcon type={'light'} icon={'plus-circle'} iconClass={'fs-1 text-gray-400 me-0'}/>
                                    </div>
                                    <p className="mb-0 text-center fs-10">{emailContent.requestNewGridText ?? ""}</p>
                                </Button>
                            </RequestButtonHOC>
                        </Col>
                    </Row>
                    {postError ? <p id="mail_select_e" className="invalid-feedback d-block">{emailContent.fillGridError ?? ""}</p> : ''}
                </div>

            </>
    }
}

const mapStateToProps = (state) => {
    return {
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
        // userData: state.authorized?.userData,
        // active_email: state.emailList?.active_email,
        // emailList: state.emailList?.emailList,
        // loader: state.emailList?.emailLoading,

    }
}

export default connect(mapStateToProps) (AsideContent)