import React from 'react';

const EmailPlatformInstructions = ({ platform = 'klaviyo', instructionsData }) => {
    
    const renderInstructionStep = (step, index) => {
        const { text, link, afterText, bold, bold2, afterText2 } = step;

        return (
            <li key={index} className="mb-3">
                {text && <span>{text}</span>}

                {link && (
                    <a className="fw-semibold ms-1" href={link.href} target="_blank" rel="noopener noreferrer" title={link.title || link.label}>
                        {link.label}
                    </a>
                )}

                {afterText && <span className="ms-1">{afterText}</span>}
                {bold && <span className="fw-semibold ms-1">{bold}</span>}
                {afterText2 && <span className="ms-1">{afterText2}</span>}
                {bold2 && <span className="fw-semibold ms-1">{bold2}</span>}
            </li>
        );
    };

    const platformInstructions = instructionsData?.emailPlatformInstructions?.[platform] ?? null;

    if (!platformInstructions) return null;

    return (
        <ul className="list-unstyled list-points fs-8 ms-3">
            {platformInstructions.map((step, index) => renderInstructionStep(step, index))}
        </ul>
    );
};

export default EmailPlatformInstructions;
