import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Container, Dropdown } from 'react-bootstrap';
import withRouter from '../../../routes/WithRouter';
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import { shopifyApp } from '../../../../utils';

class PublishSubheader extends Component {
  isActive(keyword) {
    return window.location.href.includes(keyword) ? 'active' : '';
  }

  render() {
    const { websiteUrl, navigate } = this.props;
    const isShopify = shopifyApp();
    const publishContent = window?.APP_CONTENT?.publish ?? {}

    return (
      <div className="subheader">
        <Container fluid>
          <div className="nav nav-underline d-none d-lg-flex">
            <div className="nav-item">
              <Link to={`/website/${websiteUrl}/${localStorage.getItem('website_id') || ''}`} className={`nav-link ${this.isActive('website')}`} title={publishContent.websiteLabel ?? ""}>
                <FontAwesomeIcon icon={'browser'} iconClass="me-1" />
                {publishContent.websiteLabel ?? ""}
              </Link>
            </div>
            <div className="nav-item">
              <Link to={`/shopon-bio/preview/${localStorage.getItem('shopOn_id') || ''}`} className={`nav-link ${this.isActive('shopon-bio')}`} title={publishContent.shoponBioLabel ?? ""}>
                <FontAwesomeIcon icon={'link'} iconClass="me-1" />
                {publishContent.shoponBioLabel ?? ""}
              </Link>
            </div>
            <div className="nav-item">
              <Link to={`/email-campaign/publish/${localStorage.getItem('emailCamp_id') || ''}`} className={`nav-link ${this.isActive('email-campaign')}`} title={publishContent.emailCampaignLabel ?? ""}>
                <FontAwesomeIcon icon={'bullhorn'} iconClass="me-1" />
                {publishContent.emailCampaignLabel ?? ""}
              </Link>
            </div>
            <div className="nav-item">
              <Link to={`/more-publish/`} className={`nav-link ${this.isActive('more-publish')}`} title={publishContent.moreLabel ?? ""}>
                <FontAwesomeIcon icon={'ellipsis'} iconClass="me-1" />
                {publishContent.moreLabel ?? ""}
              </Link>
            </div>
          </div>
          <Dropdown className="d-flex d-lg-none" style={{ marginTop: isShopify ? '0' : '5px', position: isShopify ? 'relative' : '', top: isShopify ? '6px' : '' }} >
            <Dropdown.Toggle variant="secondary" id="subheader-tabs">
              {window.location.href.includes('website') ? (publishContent.websiteLabel ?? "") : window.location.href.includes('shopon-bio') ? (publishContent.shoponBioLabel ?? "") : window.location.href.includes('email-campaign') ? (publishContent.emailCampaignLabel ?? "") : window.location.href.includes('more-publish') ? (publishContent.moreLabel ?? "") : ''}
            </Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item onClick={() => navigate(`/website/${websiteUrl}/${localStorage.getItem('website_id') || ''}`)} className={this.isActive('website')} >
                <FontAwesomeIcon icon={'browser'} iconClass="me-1" />
                {publishContent.websiteLabel ?? ""}
              </Dropdown.Item>
              <Dropdown.Item onClick={() => navigate(`/shopon-bio/preview/${localStorage.getItem('shopOn_id') || ''}`)} className={this.isActive('shopon-bio')} >
                <FontAwesomeIcon icon={'link'} iconClass="me-1" />
                {publishContent.shoponBioLabel ?? ""}
              </Dropdown.Item>
              <Dropdown.Item onClick={() => navigate(`/email-campaign/publish/${localStorage.getItem('emailCamp_id') || ''}`)} className={this.isActive('email-campaign')} >
                <FontAwesomeIcon icon={'bullhorn'} iconClass="me-1" />
                {publishContent.emailCampaignLabel ?? ""}
              </Dropdown.Item>
              <Dropdown.Item onClick={() => navigate(`/more-publish/`)} className={this.isActive('more-publish')} >
                <FontAwesomeIcon icon={'ellipsis'} iconClass="me-1" />
                {publishContent.moreLabel ?? ""}
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </Container>
      </div>
    );
  }
}

export default withRouter(PublishSubheader);
