import React, { Component } from 'react'
import { Modal, Button, Col, Row, InputGroup, Form } from 'react-bootstrap'
import { connect } from "react-redux";
import AttachMedia from './Uploads/AttachMedia'
import { containsSpecialChars, TOAST_ERROR, TOAST_SUCCESS, validateEmail } from '../../../../utils';
import { FEATURE_REQUEST } from '../../../../actions/api';
import HttpClient from '../../../../actions/http-client';

class ContactModal extends Component {
  constructor(props) {
      super(props);
      this.state = {
        name:this.props.authorized?.userData?.user_data?.user.name,
        email:this.props.authorized?.userData?.user_data?.user.emailId,
        description:'',
        uploadedMedia:'',
        uploadedUrl:'',
        nameErr:false,
        emailErr:false,
        descriptionErr:false,
        isLoading:false
      };
  }
  content = window?.APP_CONTENT?.common ?? {}

  submitForm = () => {
      const {name, email, description, uploadedMedia} = this.state

      let nameValidation = name && !containsSpecialChars(name) && String(name).trim() != 0 ? true : false
      let emailValidation = email && validateEmail(email) ? true : false
      let descriptionValidation = description && description.length <= 500 ? true : false

      if (nameValidation && emailValidation && descriptionValidation) {
        this.setState({ isLoading:true })
        const request = { 'name': name, 'email': email, 'description':`<b>${this.props.title}</b>-${description}`, 'file': uploadedMedia }
        new HttpClient().post(FEATURE_REQUEST, request).then((res) => {
          if (res.data.responseCode == 200) {
          TOAST_SUCCESS(res.data?.responseMessage ?? "")
            setTimeout(() => {
              this.setState({
                isLoading:false,
                name:this.props.authorized?.userData?.user_data?.user.name,
                email:this.props.authorized?.userData?.user_data?.user.emailId,
                description:'',
                uploadedMedia:'',
                uploadedUrl:'',
                nameErr:false,
                emailErr:false,
                descriptionErr:false
              })
              this.props.onCloseModel(false)
            }, 1000);
          } else {
            this.setState({ isLoading: false });
            TOAST_ERROR(res.data?.responseMessage ?? "");
          }
        }).catch((error) => {
          this.setState({ isLoading: false });
          TOAST_ERROR(error?.response?.data?.responseMessage ?? this.content?.some_error_occured ?? "");
        })
      }else{
        this.setState({
            isLoading:false,
            nameErr: !nameValidation,
            emailErr: !emailValidation,
            descriptionErr: !descriptionValidation
        });
      }
    }
  
  onClickToupload = (value, callBack) => this.setState({ uploadedMedia: value }, () => callBack())

  onCloseModelFun = () => {
      this.setState({
        name:this.props.authorized?.userData?.user_data?.user.name,
        email:this.props.authorized?.userData?.user_data?.user.emailId,
        description:'',
        uploadedMedia:'',
        uploadedUrl:'',
        nameErr:false,
        emailErr:false,
        descriptionErr:false
      })
      this.props.onCloseModel(false)
  }
  render() {
    const { show, title } = this.props
    const {name, email, description, nameErr, emailErr, descriptionErr, isLoading, uploadedMedia} = this.state
    const { content } = this;

    return (<Modal id="reqFeature" size="lg" show={show} aria-labelledby="reqFeature" tabIndex="0" centered backdrop={'static'} onHide={this.onCloseModelFun}>
        <Modal.Header closeButton>
          <Modal.Title>{title ?? content?.request_feature ?? ""}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="p-lg-7 p-3 demo_schedule position-relative">
          <Row className="mb-3">
            <Form.Group as={Col} md="6">
              <Form.Label>{content?.full_name ?? ""} <strong className="text-danger">*</strong></Form.Label>
              <Form.Control
                id="r_f_name"
                required
                type="text"
                placeholder={content?.full_name ?? ""}
                className={nameErr ? 'is-invalid' : ''}
                value={name} onChange={(e) => this.setState({name:e.target.value, nameErr:false})}
              />
              {nameErr && <div className="invalid-feedback">{content?.fill_full_name ?? ""}</div>}
            </Form.Group>
            <Form.Group as={Col} md="6">
              <Form.Label>{content?.email ?? ""} <strong className="text-danger">*</strong></Form.Label>
              <Form.Control
                id="r_f_email"
                required
                type="email"
                placeholder="example@example.com"
                className={emailErr ? 'is-invalid' : ''}
                value={email} onChange={(e) => this.setState({email:e.target.value, emailErr:false})}
              />
              {emailErr && <div className="invalid-feedback">{email != '' ? content?.valid_email ?? "" : content?.enter_email ?? ""}</div>}
            </Form.Group>
          </Row>
            <Form.Group as={Col} className="mb-3">
              <Form.Label>{content?.description ?? ""} <strong className="text-danger">*</strong></Form.Label>
              <Form.Control as={'textarea'} id="r_f_desc"
                placeholder={content?.enter_desc ?? ""}
                className={`${descriptionErr ? 'is-invalid' : ''} textarea-min-row-6`} maxLength={500} onChange={(e) => this.setState({description:e.target.value, descriptionErr:false})} value={description}
              />
              <div className="f-between">
                <div className="invalid-feedback d-block">{descriptionErr ? content?.field_req ?? "" : ''}</div>
                <span className="fs-10 text-muted pt-1">{description?.length}/500</span>
              </div>
            </Form.Group>
            <Form.Group as={Col} className="mb-3">
              <AttachMedia id="sugg_doc" imageStyle={{ paddingBottom: '66%' }} uploadedMedia={uploadedMedia} onClickToupload={this.onClickToupload} text={content?.upload_file ?? ""} allowFiles={['image/*', '.docx', '.pdf', '.mp4', '.mov']} isLocation={true} />
            </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button id="r_f_cancel" variant='secondary' className='ms-3 btn-min-w' onClick={this.onCloseModelFun}>{content?.cancel ?? ""}</Button>
          <Button id="r_f_submit" className={`ms-3 btn-min-w ${isLoading? 'spinner' :''}`} onClick={this.submitForm}>{content?.submit ?? ""}</Button>
        </Modal.Footer>
      </Modal>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    authorized: state.authorized,
  }
}
export default connect(mapStateToProps)(ContactModal);