import { Component } from "react";
import { Button, Col, Image, Row } from "react-bootstrap";
import { baseUrl } from "../../../../../Assets/AssetsHelper";

class PlatformAside extends Component{
    render(){
        const {selectedPlatform, onSelectedPlatform} = this.props
        const platforms = ["klaviyo", "omnisend", "hubspot", "active", "mailchimp", "getresponse", "other"]
        const pageContent = window?.APP_CONTENT?.publish ?? {};
        const { galleryTypeObj = {} } = pageContent;

        return <div className='p-4 mb-4'>
                    <h5>{galleryTypeObj?.commonContent?.sideBarPlateform ?? ""}</h5>
                    <Row>
                        {platforms.map((platform, index) => (
                            <Col sm={6}>
                                <div className="mb-3">
                                        <Button key={index} data-platform={platform} variant="secondary" className={`w-100 email-platform ${platform == selectedPlatform ? 'active' : ''}`} onClick={() => onSelectedPlatform(platform)}>
                                            <Image className="h-30px" data-mode="light" src={baseUrl(`media/icons/email-platform/${platform}.svg?`)} height={34} width={280} fluid/>
                                            <Image className="h-30px" data-mode="dark" src={baseUrl(`media/icons/email-platform/${platform}-light.svg?`)} height={34} width={280} fluid/>
                                        </Button>
                                </div>
                            </Col>
                        ))}
                    </Row>
                    
                </div>
    }
}
export default PlatformAside